import React, { useState, useEffect } from 'react';
import { useWeb3 } from "@/contexts/web3Context";
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { 
  Home as HomeIcon, 
  History, 
  Box, 
  Truck, 
  Store, 
  ShoppingCart,
  LogOut,
  User
} from "lucide-react";
import Web3 from "web3";

// Role-specific dashboard components
const ProducerDashboard = ({ contract, account }) => {
  const [productName, setProductName] = useState("");
  const [productPrice, setProductPrice] = useState("");
  const [products, setProducts] = useState([]);

  const createProduct = async () => {
    try {
      await contract.methods
        .createProduct(productName, productPrice)
        .send({ from: account });
      alert("Product created successfully!");
      setProductName("");
      setProductPrice("");
    } catch (error) {
      console.error("Error creating product:", error);
    }
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Box className="w-5 h-5" />
            Create New Product
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <Input
            placeholder="Product Name"
            value={productName}
            onChange={(e) => setProductName(e.target.value)}
          />
          <Input
            placeholder="Product Price"
            type="number"
            value={productPrice}
            onChange={(e) => setProductPrice(e.target.value)}
          />
          <Button onClick={createProduct} className="w-full">Create Product</Button>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>My Products</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {products.map((product, index) => (
              <Card key={index}>
                <CardContent className="p-4">
                  <p className="font-semibold">{product.name}</p>
                  <p className="text-sm text-gray-500">{product.price} ETH</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

const TransporterDashboard = ({ contract, account }) => {
  const [productId, setProductId] = useState("");

  const transportProduct = async () => {
    try {
      await contract.methods.transportProduct(productId).send({ from: account });
      alert("Product transported successfully!");
      setProductId("");
    } catch (error) {
      console.error("Error transporting product:", error);
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Truck className="w-5 h-5" />
          Transport Products
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        <Input
          placeholder="Product ID"
          value={productId}
          onChange={(e) => setProductId(e.target.value)}
        />
        <Button onClick={transportProduct} className="w-full">
          Transport Product
        </Button>
      </CardContent>
    </Card>
  );
};

